import React, {Component} from "react";
import {OraMessage} from "eshop/components/OraCommonComponents";

class ErrorRow extends Component {
    constructor(props) {
        super(props);
    }

    getMessageProps() {
        if(this.props.showErrors && this.props.errors && this.props.errors.length > 0) {
            return ({className: "u-margin-top-s", type: this.props.errors[0].level, text: this.props.errors[0].message});
        } else {
            return ({className: "u-margin-top-s", type: "info"});
        }
    }

    render() {
        return (
            <div className={this.props.className}>
                {this.props.children}
                <OraMessage {...this.getMessageProps()}/>
            </div>
        );
    }
}

ErrorRow.defaultProps = {
    showErrors: true
};

export default ErrorRow;
